import generateSelectors from './selector-lib';

const eventsToRecord = {
  CLICK: 'click',
  // // DBLCLICK: 'dblclick',
  // CHANGE: 'change',
  KEYDOWN: 'keydown',
  // SELECT: 'select',
  // SUBMIT: 'submit',
  // URLHASH: 'hashchange',
  // URLCHANGE: 'popstate',
  // LOAD: 'load',
  // UNLOAD: 'unload'
};

const functionify = obj => {
  for (let k in obj) {
    if (obj[k] && typeof obj[k] === 'object') {
      functionify(obj[k]);
    } else {
      if (obj[k] && {}.toString.call(obj[k]) === '[object Function]') {
        // commenting this out as high volume data makes it harder to read/debug other things
        // obj[k] = '' + obj[k];
        obj[k] = '{{Function}}';
      }
    }
  }
};

// const assertClassName = 'assertly-highlighter';

class EventRecorder {
  constructor() {
    this.eventLog = [];
    this.componentEventCache = [];
    this.previousComponent = null;
    this.previousEvent = null;
    this.dataAttribute = null;
    // this.injectHighlightingCss();
  }

  start = () => {
    this._initializeRecorder();
  };

  _initializeRecorder = () => {
    const events = Object.values(eventsToRecord);
    if (!window.recorderAddedControlListeners) {
      this.addAllListeners(events);
      window.recorderAddedControlListeners = true;
    }

    // window.addEventListener('message', this.receiveMessage.bind(this), true);

    this.sendMessage({ control: 'event-recorder-started' });
    this.sendMessage({
      control: 'get-current-url',
      href: window.location.href
    });
    this.sendMessage({
      control: 'get-viewport-size',
      coordinates: { width: window.innerWidth, height: window.innerHeight }
    });
    console.log('initialize recorder')
  };

  // injectHighlightingCss() {
  //   const head = document.head;
  //   const style = document.createElement('style');
  //   style.type = 'text/css';
  //   const css = `
  //   .${assertClassName} {
  //     border: 3px solid #3f51b5;
  //   }`;
  //   head.appendChild(style);
  //   if (style.styleSheet) {
  //     // This is required for IE8 and below.
  //     style.styleSheet.cssText = css;
  //   } else {
  //     style.appendChild(document.createTextNode(css));
  //   }
  // }

  // addClass(event) {
  //   event.target.classList.add(assertClassName);
  // }

  // removeClass(event) {
  //   event.target.classList.remove(assertClassName);
  // }

  // captureAssertion() {
  //   this.removeAllListeners(Object.values(eventsToRecordTmp));
  //   document.body.addEventListener('mouseover', this.addClass, true);
  //   document.body.addEventListener('mouseout', this.removeClass, true);
  // }

  // stopCaptureAssertion() {
  //   document.body.removeEventListener('mouseover', this.addClass, true);
  //   document.body.removeEventListener('mouseout', this.removeClass, true);
  //   const elems = document.getElementsByClassName(assertClassName);
  //   [].forEach.call(elems, function(el) {
  //     el.classList.remove(assertClassName);
  //   });
  //   this.addAllListeners(Object.values(eventsToRecordTmp));
  // }

  clearEventCache = () => {
    this.componentEventCache = [];
    console.log('clear event cache')
  };

  reconcileEventCache = e => {
    const reactComponent = findReactElement(e.target);
    if (reactComponent !== this.previousComponent) {
      this.componentEventCache.forEach(msg => {
        this.sendEvent(msg);
        this.sendMessage(msg);
      });
      this.clearEventCache();
    }
    console.log('reconcile event cache')
  };

  addAllListeners = events => {
    events.forEach(type => {
      window.addEventListener(type, this.recordEvent, true);
    });
    window.addEventListener('blur', this.reconcileEventCache, true);
    window.addEventListener('focus', this.reconcileEventCache, true);
    window.addEventListener('change', this.reconcileEventCache, true);
    console.log('add all listeners')
  };

  removeAllListeners = events => {
    events.forEach(type => {
      window.removeEventListener(type, this.recordEvent, true);
    });
    console.log('remove all listeners')
  };

  // receiveMessage(event) {
  //   const msg = JSON.parse(event.data);
  //   if (msg && msg.hasOwnProperty('assert')) {
  //     if (msg.assert) {
  //       this.captureAssertion();
  //     } else {
  //       console.log(msg);
  //       this.stopCaptureAssertion();
  //     }
  //   }
  // }

  sendMessage = msg => {
    try {
      window.parent.postMessage(JSON.stringify(msg), '*');
      this.eventLog.push(JSON.stringify(msg));
      console.log('send message')
    } catch (err) {
      console.log('caught error', err);
    }
    
  };

  getComponentName = fiber => {
    if (!fiber) return null;

    if (fiber.elementType && fiber.elementType.name) {
      return fiber.elementType.name;
    }

    if (fiber.elementType && fiber.elementType.displayName) {
      return fiber.elementType.displayName;
    }

    if (fiber.type.displayName) {
      return fiber.type.displayName;
    }

    if (fiber.type.name) {
      return fiber.type.name;
    }
    console.log('get component name')
    return this.getComponentName(fiber._debugOwner);
  };

  getComponentProps = (component, props = {}) => {        
    if (!component) return props;
    const newProps = component.pendingProps ? { ...props, ...component.pendingProps } : props;
    delete newProps.children;
    // functionify(newProps);

    if (
      component.type.__proto__ &&
      ['Component', 'PureComponent'].includes(component.type.__proto__.name)
    ) {
      return newProps;
    }
    console.log('get component props')
    return this.getComponentProps(component._debugOwner, newProps);
  };

  recordEvent = e => {    
    if (this.previousEvent && this.previousEvent.timeStamp === e.timeStamp) return;
    if (e.type === 'load' && e.target.nodeName === '#document') return;
    // we explicitly catch any errors and swallow them, as none node-type events are also ingested.
    // for these events we cannot generate selectors, which is OK
    try {
      const selectors = generateSelectors(e, this.dataAttribute);

      if (selectors && selectors.length > 0 && selectors[0] && selectors[0].match('assertly')) {
        return;
      }
      
      /*
         Click events on a dom node in a form trigger change events in the underlying form element
           this prevents duplicate events fired, as we really care more about the mouse event.

           The logic here needs to be improved but ok for immediate test case.  We should also send an update
             with additional data from the 2nd event to update the initial event instead.
       */
      // this needs to be improved but ok for immediate test case
      // returns if you clicked the same thing within 100 ms
      if (this.previousEvent) {
        if (
          (e.type === 'change' || e.type === 'click') &&
          this.previousEvent &&
          this.previousEvent.action === 'click' &&
          new Date().getTime() - this.previousEvent.timestamp < 100
        ) {
          console.log('message not send due to time')
          return;
        }
      }

      const reactComponent = findReactElement(e.target);

      const target = e.target || e.srcElement;
      const textContent = target.textContent || target.innerText;
      const props = this.getComponentProps(reactComponent);

      const msg = {
        filename:
          reactComponent && reactComponent._debugSource && reactComponent._debugSource.fileName,
        componentName: this.getComponentName(reactComponent._debugOwner),
        props: this.getComponentProps(reactComponent),
        selector: selectors,
        value: e.target.value,
        checked: e.target.hasOwnProperty('checked') ? e.target.checked : null,
        tagName: e.target.tagName,
        tagType: e.target.type,
        action: e.type,
        keyCode: e.keyCode ? e.keyCode : null,
        href: e.target.href ? e.target.href : null,
        textContent,
        timestamp: new Date().getTime(),
        coordinates: getCoordinates(e)
      };
      
      console.log('message', msg)
      localStorage.setItem('lastEvent', JSON.stringify(msg));
      this.previousEvent = msg;

      if (
        !this.previousComponent ||
        (this.previousComponent && this.previousComponent === reactComponent)
      ) {
        this.previousComponent = reactComponent;
        if (this.previousComponent && msg.checked !== null && msg.action === 'click') {
          console.log('repeated click action message sent')
          this.sendEvent(msg);
          this.sendMessage(msg);
          return
        }
        this.componentEventCache.push(msg);

        return;
      } else {
        this.clearEventCache();
      }

      this.previousComponent = reactComponent;

      // const observations = this.generateObservations(e, reactComponent);
      // this.sendEvent(msg, observations);

      this.sendEvent(msg);
      this.sendMessage(msg);

    } catch (e) {
      console.error('Error recording event', e);
    }
  };

  generateObservations = (e, reactComponent) => {
    const observations = [];

    // TODO replace this with selector from react component scoped selector
    const firstClass = e.target.className.split(' ')[0];
    let selector = e.target.nodeName.toLowerCase();
    if (firstClass) {
      selector = selector + '.' + firstClass;
    }

    if (e.type === 'change') {
      observations.push({
        component:
          reactComponent && reactComponent._debugSource && reactComponent._debugSource.fileName,
        selector: selector,
        observationType: 'valueChange',
        value: e.target.value
      });
    }
    console.log('generate observations')
    return observations;
  };

  sendEvent = async (msg, observations) => {
    const accountId = window['dataLayer'][0]['apiKey'];
    const url = `//localhost:3002/api/accounts/${accountId}/events/`;

    await fetch(url, {
      method: 'POST',
      mode: 'cors',
      cache: 'no-cache',
      credentials: 'same-origin',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        url: window.location.href,
        payload: msg,
        // TODO: test set id should be set when creating/loading testset in the runner
        testSetId: '60652b19-98f3-40b4-88f7-88c71dbc788a'
      })
    });

    if (observations && observations.length) {
      this.sendObservations(observations);
    }
    console.log('send event')
  };

  sendObservations = async observations => {
    const accountId = window['dataLayer'][0]['apiKey'];
    const url = `//localhost:3002/api/accounts/${accountId}/observations/`;

    for (let x = 0; x < observations.length; x++) {
      const observation = observations[x];
      await fetch(url, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          url: window.location.href,
          payload: observation,
          // TODO: test set id should be set when creating/loading testset in the runner
          testSetId: '60652b19-98f3-40b4-88f7-88c71dbc788a'
        })
      });
    }
    console.log('send observations')
  };

  getEventLog = () => {
    console.log('get event log')
    return this.eventLog;
  };

  clearEventLog = () => {
    console.log('clear event log')
    this.eventLog = [];
  };
}

function getCoordinates(evt) {
  const eventsWithCoordinates = {
    mouseup: true,
    mousedown: true,
    mousemove: true,
    mouseover: true
  };
  return eventsWithCoordinates[evt.type] ? { x: evt.clientX, y: evt.clientY } : null;
}

// map react classes to dom elements
function findReactElement(node) {

  for (var key in node) {

    if (key.startsWith('__reactInternalInstance$')) {
      // console.log('keys in the find ReactElement', node[key])
      return node[key];
    }
  }
  return null;
}

window.eventRecorder = new EventRecorder();
window.eventRecorder.start();
